}

.menuComponent {
  width: 15%;
  padding: 0.5rem 0;
  padding-right: 0.5rem;
  height: 900px;
}

.kategory{
  height: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 0.5rem;
  position: relative;
  color: rgb(29, 95, 170)
}

.makanan, .minuman {
  margin-top: 5px;
  margin-bottom: 0.5rem;
  height: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 0.5rem;
